

















































































import Vue from "vue";
import axios from "axios";
export default Vue.extend({
  mounted() {
    this.getSetting();
  },
  data() {
    return {
      form: {
        serverPort: 0,
        url: "",
        wavePort: 123,
        epochPort: 1234,
        recordLen: 1,
        timeZone: 8,
        recordPath: "",
        modify: 0,
      },
    };
  },
  methods: {
    getSetting() {
      axios
        .get("/setting")
        .then((res) => {
          const ret = res.data;
          this.form.serverPort = ret.serverPort;
          this.form.url = ret.url;
          this.form.wavePort = ret.wavePort;
          this.form.epochPort = ret.epochPort;
          this.form.recordLen = ret.recordLen;
          this.form.recordPath = ret.recordPath;
          this.form.modify = ret.modify;
        })
        .catch((err) => {
          alert(err);
        });
    },
    saveSetting() {
      axios
        .post("/setting", this.form)
        .then((res) => {
          const ret = res.data;
          if (ret.ok)
            this.$bvModal.msgBoxOk("Success!").then(() => {
              this.$emit("hideModal");
            });
        })
        .catch((err) => alert(err));
    },
  },
});
