






































import Vue from "vue";
export default Vue.extend({
  data() {
    return {
      modalTitle: "",
    };
  },
  methods: {
    updateSetting() {
      this.modalTitle = "Setting";
      this.$bvModal.show("modal");
    },
    updateBackground() {
      this.modalTitle = "Background";
      this.$bvModal.show("modal");
    },
    hideModal() {
      this.$bvModal.hide("modal");
    },
  },
});
